<template>
  <CardBlock class="release-card">
    <template #notice>
      <CardNotice v-if="cardParams.notice" :type="cardParams.notice.type">
        {{ cardParams.notice?.text }}
      </CardNotice>
    </template>

    <template #title>
      <CardTitle v-if="props.title" :badges="cardParams.titleBages" modalLink @click="emit('showChangelog', props.uid)">
        {{ props.title }}
      </CardTitle>
    </template>

    <template #actions>
      <ButtonBase
        v-if="cardParams.actions?.install"
        :icon-name="props.status === 'pending' ? 'change' : 'check'"
        btn-style="accent"
        iconPositionLeft
        :disabled="installDisabled || props.status === 'pending'"
        @click="emit('installRelease', props.uid)"
      >
        {{ props.status === "pending" ? $t("releases.card.button.install_in_progress") : $t("releases.card.button.install") }}
      </ButtonBase>
    </template>

    <template #content>
      <div v-if="props.content.length" class="release-card__content">
        <ParamsItem
          v-for="(item, i) in props.content"
          :key="i"
          class="release-card__content-item"
          :title="item.title"
          :value="item.value"
          :bulleted="false"
        />
      </div>
    </template>
  </CardBlock>
</template>

<script setup lang="ts">
import CardBlock from "@/shared/ui_kit/card/CardBlock.vue";
import CardNotice from "@/shared/ui_kit/card/CardNotice.vue";
import CardTitle from "@/shared/ui_kit/card/CardTitle.vue";
import ButtonBase from "@/shared/ui_kit/ButtonBase.vue";
import ParamsItem, { type ParamItemInterface } from "@/shared/ui_kit/params/ParamsItem.vue";

import { computed } from "vue";
import type releaseStatuses from "@/pages/releases/releaseStatuses";
import type messageStatusTypes from "@/shared/ui_kit/card/messageStatusTypes";
import { useI18n } from "vue-i18n";

const { t } = useI18n();

export interface ReleaseCardInterface {
  uid: string;
  status: (typeof releaseStatuses)[number];
  title?: string;
  content: ParamItemInterface[];
  installDisabled?: boolean;
}

const props = withDefaults(defineProps<ReleaseCardInterface>(), {
  status: "new",
  content: () => [],
  installDisabled: false,
});

const emit = defineEmits<{
  (e: "installRelease", uid: typeof props.uid): void;
  (e: "showChangelog", uid: typeof props.uid): void;
}>();

type MessageStatusType = (typeof messageStatusTypes)[number];
type StatusParams = {
  [key in (typeof releaseStatuses)[number]]: {
    notice?: { type: MessageStatusType; text: string };
    titleBages?: { type: MessageStatusType; value: string }[];
    actions?: { install: boolean };
  };
};

const statusParams: StatusParams = {
  new: {
    notice: {
      type: "positive",
      text: t("releases.card.notice.new"),
    },
    titleBages: [{ type: "positive", value: t("releases.card.bage.new") }],
    actions: { install: true },
  },
  dangerous: {
    notice: {
      type: "additional",
      text: t("releases.card.notice.dangerous"),
    },
    titleBages: [{ type: "additional", value: t("releases.card.bage.dangerous") }],
    actions: { install: true },
  },
  current: {
    titleBages: [{ type: "accent", value: t("releases.card.bage.current") }],
  },
  pending: {
    notice: {
      type: "blocked",
      text: t("releases.card.notice.pending"),
    },
    titleBages: [{ type: "blocked", value: t("releases.card.bage.pending") }],
    actions: { install: true },
  },
  outdated: {
    titleBages: [{ type: "blocked", value: t("releases.card.bage.outdated") }],
  },
};

const cardParams = computed(() => statusParams[props.status]);
</script>

<style scoped lang="scss">
.release-card__content {
  display: flex;
  flex-wrap: wrap;

  gap: 30px;
}

.release-card__content-item {
  width: 352px;
}
</style>
